import React, { Component } from 'react';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import './style.event.css';


export class Event extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cardHover: false,
            showModal: false
        }

    }
    cardStyle = {
        width: "18rem",
        margin: "10px",
        marginTop: "20px",
        background: "white",
        cursor: this.hover ? 'pointer': 'unset',
        boxShadow: `0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)`
    }

  
    handleClick = (event) => {

    }
    renderDescription(description) {
        if(description.length > 64) {
            return <Button>See Description</Button>
        } else {
            return <Card.Text>{description}</Card.Text>;
        }
    }
    showModal = () => {
        this.setState({showModal: true});
    }
    hideModal = () => {
        this.setState({showModal: false});
    }

    render() {
        
        
        return (
            <React.Fragment>
                <Card style={this.cardStyle} onClick={(event) => this.handleClick(event)}>
                    <Card.Body className="hoverable">
                        <Card.Title>
                            {this.props.info?.title ? this.props.info?.title : "Event Title Placeholder"}
                        </Card.Title>
                        <Card.Subtitle className="bm-2 text-muted" style={{paddingBottom: "10px"}}>
                            {this.props.info?.location ? this.props.info?.location : "Event Location TBA"}
                        </Card.Subtitle>
                        <Card.Subtitle className="bm-2 text-muted" style={{paddingBottom: "10px"}}>
                            {this.props.info?.date ? 
                                <span>{parseInt(this.props.info?.date?.month_name)} {parseInt(this.props.info?.date?.day_qualified)} </span>: "Event Date TBA"}
                        </Card.Subtitle>
                        <Card.Subtitle className="bm-2 text-muted" style={{paddingBottom: "10px"}}>
                            {this.props.info?.date ? 
                                <span>{this.props.info?.date?.hour_start}:{this.props.info?.date?.minute_start} {this.props.info?.date?.start_period.toUpperCase()} to {this.props.info?.date?.hour_end}:{this.props.info?.date?.minute_end} {this.props.info?.date?.end_period.toUpperCase()}</span> : "Event Time TBA"}
                        </Card.Subtitle>
                    

                            {this.props.info?.description ? this.renderDescription(this.props.info?.description) : ""}
                            <Button onClick={ (event) => this.showModal()} style={{float:"right"}} variant="info">More Info</Button>
                        
                    </Card.Body>
                </Card>
                <Modal
                    show={this.state.showModal}
                    onHide={ () => this.hideModal()}
          
                 
                    >
                    <Modal.Header>
                        <Modal.Title id="modal-title">
                            Event Title
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        This is the body
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={() => this.hideModal()}>Close</Button>
                    </Modal.Footer>
                </Modal>
            </React.Fragment>
        )
    }
}

export default Event
