import React, {useState} from 'react';
import Collapse from '@material-ui/core/Collapse';
import Paper from '@material-ui/core/Paper';
import './Row.css';


const Row = (props) => {
    const [open, setOpen] = useState(false);
    return (
        
        <Paper elevation={4} onClick={() => {
            console.log("toggle");
            setOpen(!open);
        }} className="table-row">
            <div className="always-shown">
                <div className="title-block">
                    <span className="title">
                        {props.data.project_title}
                    </span>
                    <span className="contact">
                        <span>{props.data.research_contact}  </span><a href={`mailto:${props.data.contact_email}`}>({props.data.contact_email})</a> 
                    </span>
                </div>
                

            </div>
            <Collapse as="div" in={open}
                style={{width: "100%"}}

            >
                <div className="inner-content">
                    {
                        props.data.website ? <div className="data-item">
                        <span className="data-title">Website:</span>
                        <div className="data"><a href={props.data.website} target="_blank">Website</a></div>
                        </div> : ""
                    }
                    
                
                    <div className="data-item">
                        <span className="data-title">Project Thematic Area:</span>
                        <div className="data">{props.data.project_thematic_area ? props.data.project_thematic_area : "Not Given"}</div>
                    </div>
                    {/* <div className="data-item">
                        <span className="data-title">Associated Purdue College:</span>
                        <div className="data">{props.data.project_college ? props.data.project_college : "Not Given"}</div>
                    </div> */}
                    <div className="data-item">
                        <span className="data-title">Associated Purdue Department:</span>
                        <div className="data">{props.data.purdue_department ? props.data.purdue_department : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Project Description:</span>
                        <div className="data">{props.data.project_description ? props.data.project_description : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Participation Venue (Online/In-Person/Hybrid):</span>
                        <div className="data">{props.data.location ? props.data.location : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Semesters:</span>
                        <div className="data">{props.data.semesters ? props.data.semesters.map( (element, index) => {return(<div key={index}>{element}<br/></div>)}) : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Time Commitment:</span>
                        <div className="data">{props.data.time_commitment ? props.data.time_commitment : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">GPA Requirement</span>
                        <div className="data">{props.data.gpa ? props.data.gpa : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Eligible Majors:</span>
                        <div className="data">{props.data.eligible_majors ? props.data.eligible_majors.map( (element, index) => {return(<div key={index}>{element}<br/></div>)}) : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Preferred Majors:</span>
                        <div className="data">{props.data.preferred_major && (props.data.preferred_major != "") ? props.data.preferred_major.map( (element, index) => {return(<div key={index}>{element}<br/></div>)}) : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Class Standing:</span>
                        <div className="data">{props.data.class_standing ? props.data.class_standing.map( (element, index) => {return(<div key={index}>{element}<br/></div>)}) : "Not Given"}</div>
                    </div>
                    
                    <div className="data-item">
                        <span className="data-title">Compensation Category:</span>
                        <div className="data">{props.data.compensation_category ? props.data.compensation_category : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Compensation Details Academic Credit:</span>
                        <div className="data">{props.data.compensation_details.academic_credit ? props.data.compensation_details.academic_credit : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Compensation Details Hourly Rate:</span>
                        <div className="data">{props.data.compensation_details.hourly_rate ? props.data.compensation_details.hourly_rate : "Not Given"}</div>
                    </div>
                    <div className="data-item">
                        <span className="data-title">Compensation Details Stipend:</span>
                        <div className="data">{props.data.compensation_details.stipend ? props.data.compensation_details.stipend : "Not Given"}</div>
                    </div>
                    
                    
                    
                </div>
               
            </Collapse>
        </Paper>
        
    )
}

export default Row;