import React from 'react'




function PageContent(props) {
    const style_pageContent = {
        padding: "25px",
        paddingLeft: props?.paddingLeft ? props?.paddingLeft : "40px",
        flexGrow: "1",
        width: "100%",
        minHeight: "100%",
        boxSizing: "border-box",
        background: "#F9F9F9",
        marginTop: "10px"
        
    }
    return (
        <div style={style_pageContent}>
            {props.children}
        </div>
    )
}

export default PageContent
