import React, {useState, useEffect} from 'react';
import Row from '../components/Table/Row';
import PageContent from '../components/PageContent/PageContent';
import CustomTable from '../components/CustomTable/CustomTable';
import 'bootstrap/dist/css/bootstrap.min.css';
import './research-group.css';
import json from './rr-json.js';
import _ from 'lodash';
import Collapse from '@material-ui/core/Collapse';
import Paper from '@material-ui/core/Paper';





export const ResearchGroup  = () =>  {
    const local_json = json;
    const [originalFilterSelections, setOriginalFilterSelections] = useState(undefined);
    const [filterOpen, setFilterOpen] = useState(false);
    const [filterState, setFilterState] = useState({});
    const [rows, setRows] = useState([]);
    
    const getJsonFilterSelections = async (json) => {
        
        let selections = {
            purdue_college: [], 
            purdue_department: [], 
            eligible_majors: [], 
            time_commitment: [], 
            semesters: [], 
            location: [], 
            compensation_category: [], 
            gpa: [], 
            preferred_major: [],
            class_standing: []
        };
        let newObj = {};
        
        for(let object of json) {
            //in each research object
            for(let key in object) {
                //in each key of the research object
                if(selections[key]) {
                    if(newObj[key] === undefined) {
                        newObj[key] = [];
                    }
                    //if the selections object also has a key like that 
                    const value_of_object_key = object[key];
                    if(typeof(value_of_object_key) === Array) {
                       
                        const length = value_of_object_key.value.length;
                        for(let i = 0; i < length; i++) {
                            newObj[key].push(value_of_object_key[i]);
                        }
                        
                    } else {
                        newObj[key].push(object[key]);
                    }
                }
                
            }
           
            
        }
       
        for(let key in newObj) {
            newObj[key] = _.uniq(_.flattenDeep(newObj[key]));

        }
        
        //returns the object with all the unique selections
        return newObj;

    }

    const setAllFiltersTrue = async (unique_filter_options) => {
        let filters = {};
        _.forIn(unique_filter_options, (value, key) => {
           
            if(!filters[key]) {
                filters[key] = [];
            }
            for(let content of value) {
                
                filters[key].push({
                    value: content, 
                    visible: true
                });
            }
        });
        console.log(filters);
        return filters;
    
    }

    useEffect( () => {
        const filters = async () => {
            //fires once when the component is added to the dom
            const filterSelections = await getJsonFilterSelections(local_json);
            // console.log(local_json);
            // console.log(filterSelections);
            await setOriginalFilterSelections(filterSelections);
            const filters_state = await setAllFiltersTrue(filterSelections);
            setFilterState(filters_state);
        }
        const init_rows_state = async () => {
            let row_state = [];
            for(let row of local_json) {
                row_state.push({
                    data: row, 
                    open: false
                });
            }
            console.log(local_json);
            await setRows(row_state);
        }
        filters();
        init_rows_state();
    }, []);



    return (
        <PageContent>
            <h1 style={{marginBottom: "20px"}}>Researchers Attending Research Roundtable</h1>
            <Paper as="div" elevation={5} className="table-container" style={{minHeight: "100vh"}}>
                {/* <div className="filter-container">
                    {let filter_change_stat = {
                        class_standing: [{
                            value: "Sophomore",
                            visible: true
                        }]
                    }
                    }
                    <div className="filter-section">
                        <span className="filter-control-title">{filterState.class_standing[0].value}</span>
                        <input type="checkbox" checked={filterState.class_standing[0].visible} onClick={() => }/>
                    </div>
                </div> */}
                <div className="table">
                    
                    {
                        rows.map( (row, index) => {
                            return (
                                <Row data={row.data} key={index}/>
                            );
                        })
                    }
                </div>
            </Paper>
        </PageContent>
    )
    
}

export default ResearchGroup;
