import React, { Component } from 'react';
import moment from 'moment';



import Event from '../components/Event/Event';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import PageContent from '../components/PageContent/PageContent';

const style_eventsDescription = {
	color: "#383838",
	fontSize: "18px",
	
	fontWeight: "450"
}
const style_eventsContainer = {
	width: "100%",
	minHeight: "200px",
	backgroundColor: "magenta"
}
const style_select = {
	width: "500px"
}
const style_dateSelectors = {
	display: "flex",
	flexDirection: "row",
	justifyContent: "flex-start",
	alignItems: "flex-start",
	height: "60px",
	width: "300px"
}


export class Events extends Component {
    constructor(props) {
        super(props);
        this.state = {
            events: [],
            filtered_events: [],
            filter_day: moment().date(),
            filter_month: moment().month(),
            filter_year: moment().year()
            
        }
        this.current_date = moment();
        this.months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
    
       
    }
    month = undefined;
    year = undefined;

    componentDidMount() {
        this.setState({events: [{
                title: "event 1 title",
                location: "event 1 location",
                date: {
                    month: 7,
                    day: 24,
                    year: 2020
                },
                time: {
                    hour: 12,
                    minute: 35
                }
            },
            {
                title: "event 2 title",
                location: "event 2 location",
                date: {
                    month: 7,
                    day: 25,
                    year: 2020
                },
                time: {
                    hour: 12,
                    minute: 35
                }
            }]
        });
    }
  

    handleChange = (event) => {
        event.preventDefault();
        this.setState({[event.target.name]: event.target.value});
        
    }


    // onChange={(event) => this.handleYearChange(event.target.value)}
    // onChange={(event) => this.handleMonthChange(event.target.value)}
    render() {
        return (
            <PageContent>
                <h1>This page is under development</h1>
                <div>
                    {this.state.filter_month} {this.state.filter_year}
                    <form onChange={(event) => this.handleChange(event)}>
                        <select name="filter_year" id="year" defaultValue={this.state.filter_year} >
                            <option value={this.current_date.year()}>{this.current_date.year()}</option>
                            <option value={this.current_date.year() + 1}>{this.current_date.year() + 1}</option>
                        </select>
                        <select name="filter_month" id="month" defaultValue={this.state.filter_month} >
                            {this.months.map( (month, index) => {
                                return <option key={index} value={index}>{month}</option>;
                            })}
                        </select>
                    </form>

                </div>
                <div id="events">
                    {this.state.events.map( (event, index) => {
                        if(parseInt(event.date.month) === parseInt(this.state.filter_month) && parseInt(event.date.year) === parseInt(this.state.filter_year)) {
                            return <React.Fragment key={index}><span>{event.title}, {event.location}, {event.date.month +1}/{event.date.day}/{event.date.year}, {event.time.hour}:{event.time.minute}</span><br/></React.Fragment>;
                        }
                    })}
					<Event/>
                </div>
            </PageContent>
        )
    }
}

export default Events
