import 'bootstrap/dist/css/bootstrap.min.css';

import React from 'react';
import './App.css';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Menu from './components/Menu/Menu';
import Home from './pages/Home';
import ResearchGroup from './pages/ResearchGroup';
import About from './pages/About';
import Contact from './pages/Contact';
import Events from './pages/Events';
import AnnualEvents from './pages/AnnualEvents';
import Announcements from './pages/Announcements';
import Sponsors from './pages/Sponsors';
import FE from './pages/FE';
import OE from './pages/OE';
import RR from './pages/RR';
import ChainReaction from './pages/ChainReaction';
// import AdminLogin from './pages/admin/admin-login/AdminLogin.js';



function App() {

  
  const appStyle = {
    boxSizing: "border-box",
    display: "flex",
    flexDirection: "column",
    justifyContent: "flex-start",
    alignItems: "flex-start",
    width: "100%",
    height: "100%",
    minHeight: "100vh",
    backgroundColor: "#F9F9F9"
 
  }
  return (
    <div style={appStyle}>
      <BrowserRouter>
        <Menu/>
        <Switch>
          <Route exact path="/announcements">
            <Announcements/>
          </Route>
          <Route exact path="/upcoming-events">
            <Events/>
          </Route>
          <Route exact path="/annual-events">
            <AnnualEvents/>
          </Route>
          <Route exact path="/chain-reaction-team">
            <ChainReaction/>
          </Route>
          <Route exact path="/research-roundtable">
            <RR/>
          </Route>
          <Route exact path="/research-roundtable/event">
            <ResearchGroup/>
          </Route>
          <Route exact path="/fe-exam-prep">
            <FE/>
          </Route>
          <Route exact path="/order-of-the-engineer">
            <OE/>
          </Route>
          <Route exact path="/contact">
            <Contact/>
          </Route>
          <Route exact path="/about">
            <About/>
          </Route>
          <Route exact path="/sponsors">
            <Sponsors/>
          </Route>
            <Route exact path="/">
              <ResearchGroup/>
            </Route>
          </Switch>
        </BrowserRouter>
      </div>

    
  );
}

export default App;
